import { slideInAnimation } from './../../../route-animations';
import { ActivatedRoute } from '@angular/router';
import { Component, AfterContentChecked} from '@angular/core';

@Component({
  selector: 'app-wave-layout',
  templateUrl: './wave-layout.component.html',
  styleUrls: ['./wave-layout.component.scss'],
  animations: [ slideInAnimation ]
})
export class WaveLayoutComponent implements AfterContentChecked {
  displayWave = false;

  constructor(private route: ActivatedRoute) {
  }

  ngAfterContentChecked() {
    const data = this.route.snapshot.firstChild.data;
    if (data !== undefined) {
      this.displayWave = data.displayBackground;
    }
  }

}
