import {
  Component,
  OnInit,
  ElementRef,
  QueryList,
  ViewChildren,
  Input
} from '@angular/core';
import { MealType, DayType } from 'src/app/types/mealTypes';

@Component({
  selector: 'app-menu-content-mobile',
  templateUrl: './menu-content-mobile.component.html',
  styleUrls: ['./menu-content-mobile.component.scss']
})
export class MenuContentMobileComponent implements OnInit {
  dayOne = true;
  dayTwo = false;
  selectedMealType = '';
  @ViewChildren('panel', { read: ElementRef }) public panel: QueryList<
    ElementRef
  >;
  @ViewChildren('slider', { read: ElementRef }) public sliderElement: QueryList<
    ElementRef
  >;
  @Input() daysList: DayType[];

  constructor() {}

  ngOnInit() {
    // this.selectedMealType = this.mealsDayTwo.find(row => row.active).typeOfMeal;
  }

  select(day) {
    day.checked = !day.checked;
    const mealsArray: MealType[] = [];
    this.daysList
      .filter(row => row.id === day.id)
      .forEach(row => {
        mealsArray.push(...row.meals);
      });
    const currentActive = mealsArray.find(row => row.active === true);

    this.selectedMealType = currentActive.typeOfMeal;
  }

  public onPreviousSearchPosition(id): void {
    const width = this.sliderElement.toArray()[id - 1].nativeElement
      .offsetWidth;

    let adjuster = 290;
    if (width === 300) {
      adjuster += 50;
    }
    this.panel.toArray()[id - 1].nativeElement.scrollTo({
      left: this.panel.toArray()[id - 1].nativeElement.scrollLeft - adjuster,
      behavior: 'smooth'
    });
    this.changeStateOfArrayBackWards(id);
  }

  public onNextSearchPosition(id: number): void {
    const width = this.sliderElement.toArray()[id - 1].nativeElement
      .offsetWidth;
    let adjuster = 290;
    if (width === 300) {
      adjuster += 50;
    }
    this.panel.toArray()[id - 1].nativeElement.scrollTo({
      left: this.panel.toArray()[id - 1].nativeElement.scrollLeft + adjuster,
      behavior: 'smooth'
    });
    this.changeStateOfArrayForward(id);
  }

  changeStateOfArrayForward(id) {
    const mealsArray: MealType[] = [];
    this.daysList
      .filter(row => row.id === id)
      .forEach(row => {
        mealsArray.push(...row.meals);
      });
    const currentActive = mealsArray.find(row => row.active === true);
    const indexOfCurrent = mealsArray.indexOf(currentActive);

    if (indexOfCurrent >= 0 && indexOfCurrent < mealsArray.length - 1) {
      mealsArray[indexOfCurrent + 1].active = true;
      currentActive.active = false;
      this.selectedMealType = mealsArray[indexOfCurrent + 1].typeOfMeal;
    }
    // const currentActive =  this.daysList.map(row => row.meals.forEach(meal => { if (meal.id === id) {return meal; }}));
    // console.log(currentActive);

    // const currentActive = null;
    // const ca = this.daysList.map(row => {
    //   if (row.id === id) {return row; }
    // }).map(selectedRow => { if (selectedRow) { return selectedRow.meals; } })
    // .map(meal => meal);
    // console.log(ca);
    // console.log(selectedRow.meals.find(row => row.active === true););
    // const currentActive = this.mealsDayTwo.find(row => row.active === true);
    // const indexOfCurrent = this.mealsDayTwo.indexOf(currentActive);
    // if (indexOfCurrent >= 0 && indexOfCurrent < this.mealsDayTwo.length - 1) {
    //   this.mealsDayTwo[indexOfCurrent].active = false;
    //   this.mealsDayTwo[indexOfCurrent + 1].active = true;
    //   this.selectedMealType = this.mealsDayTwo[indexOfCurrent + 1].typeOfMeal;
    // }
  }

  changeStateOfArrayBackWards(id) {
    const mealsArray: MealType[] = [];
    this.daysList
      .filter(row => row.id === id)
      .forEach(row => {
        mealsArray.push(...row.meals);
      });
    const currentActive = mealsArray.find(row => row.active === true);
    const indexOfCurrent = mealsArray.indexOf(currentActive);

    if (indexOfCurrent > 0 && indexOfCurrent < mealsArray.length) {
      mealsArray[indexOfCurrent].active = false;
      mealsArray[indexOfCurrent - 1].active = true;
      this.selectedMealType = mealsArray[indexOfCurrent - 1].typeOfMeal;
    }
  }
}
