import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sizes-form',
  templateUrl: './sizes-form.component.html',
  styleUrls: ['./sizes-form.component.scss']
})

export class SizesFormComponent implements OnInit {

  basic = true;
  advance = false;
  weightValue = 50;
  goalValue = 60;
  constructor() { }

ngOnInit() {
  }

select(view: string) {
  if (view === 'basic') {
    return [this.basic = true, this.advance = false];
  } else {
    return [this.basic = false, this.advance = true];
  }
  }
}
