import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-mode-form',
  templateUrl: './mode-form.component.html',
  styleUrls: ['./mode-form.component.scss']
})
export class ModeFormComponent implements OnInit {

  pair = false;
  personally = false;
  answer: boolean;
  constructor() { }

  ngOnInit() {
  }

  personallyClicked() {
    this.personally = true;
    this.pair = false;
    return [this.pair, this.personally];
  }

  pairClicked() {
    this.personally = false;
    this.pair = true;

    return [this.pair, this.personally];
  }

  select(answer: boolean) {
    if (answer === true) {
      return this.answer = true;
    } else {
      return this.answer = false;
    }
  }

  login() {

  }

}
